import React, { Suspense, lazy } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import sidebarInfo from "./sidebar-info";

const Docs = () => {
  return (
    <Suspense fallback={<div>
      <div className="Docs">
        <title>Docs | OSGL</title>
        <div className="doc-content">
        </div>
      </div>
    </div>}>
    <Routes>
        <Route index element={<Navigate to="introduction/introduction" replace />} />
        {sidebarInfo.map((element) =>
          element.links.map((sublink) => {
            const PageComponent = lazy(() => 
              import(`./pages/${element.id.split("/").pop()}/${sublink.href.split("/").pop()}.js`)
            );

            return (
              <Route
                key={sublink.href}
                path={sublink.href.split("osgl/#/docs/")[1]}
                element={<PageComponent />}
              />
            );
          })
        )}
      </Routes>
    </Suspense>
  );
};

export default Docs;