import React from "react";

const TopbarOneWayButton = ({name, link}) => {
    let colour = `/osgl/${name}-colour.png`;

    return <div
        className={name}
        onClick={() => window.location.href = link || ""}
    >
        <img
            src={colour}
            alt={name}
            className={`${name}-img`}
        />
    </div>
}

export default TopbarOneWayButton;