import React from "react";
import TopbarButton from "../../components/topbar-button";
import TopbarOneWayButton from "../../components/topbar-one-way-button";

const APITopbar = () => {
    return <div className="topbar" style={{height: "55px", position: "fixed" }}>
        <TopbarOneWayButton name="logo" link="/osgl" />

        <div className="api-sig">
            <img
                src="/osgl/api-colour.png"
                alt="api"
                className="api-img"
            />
        </div>

        <img src="/osgl/logo-sep.png" alt="Separator" className="seperator-img" style={{marginLeft: "75px"}} />

        <TopbarButton name="docs" link="/osgl/#/docs" />
        <TopbarButton name="github" link="https://github.com/osgl-rbx/osgl/" />
    </div>
}

export default APITopbar;