import React from "react";
import TopbarButton from "./topbar-button";

const Topbar = () => {
    return <div className="topbar">
        <TopbarButton name="logo" link="/osgl" />

        <img src="/osgl/logo-sep.png" alt="Separator" className="seperator-img" />

        <TopbarButton name="docs" link="/osgl/#/docs" />
        <TopbarButton name="api" link="/osgl/#/api" />
        <TopbarButton name="github" link="https://github.com/osgl-rbx/osgl/" />
    </div>
}

export default Topbar;