import React, { useState, useEffect, useRef } from "react";
import Topbar from "./components/topbar";
import Footer from "./components/footer";
import "./Landing.css";

const words = ["textures", "shapes", "sprites", "videos", "text", "pixels", "buffers", "masks", "bitmaps", "geometry"];
const colours = ["#3F8AFF", "#8CBAFF", "#5E9EFF"];
const fireColors = [
  "#FF4500", "#FF6347", "#FF7F50", "#FF8C00", "#FF4500", "#FF5733",
  "#FF6A00", "#F44336", "#FF1E00", "#FF3E00", "#FF6347"
];

const Landing = () => {
  const [index, setIndex] = useState(0);
  const [indexExtra, setIndexExtra] = useState(1);
  const [isAnimating, setIsAnimating] = useState(false);
  const [colorIndex, setColorIndex] = useState([0, 1]);
  const lettersRef = useRef([]);

  useEffect(() => {
    const startAnimation = () => {
      setIsAnimating(true);

      const newColorIndex = [
        Math.floor(Math.random() * colours.length),
        Math.floor(Math.random() * colours.length),
      ];
      setColorIndex(newColorIndex);

      startShaking();

      setTimeout(() => {
        setIndex((prevIndex) => (prevIndex + 1) % words.length);
        setIndexExtra((prevIndex) => (prevIndex + 1) % words.length);
        setIsAnimating(false);
      }, 500);

      setTimeout(startAnimation, 5000);
    };

    startAnimation();

    return () => {
      clearTimeout();
    };
  }, []);

  const startShaking = () => {
    lettersRef.current.forEach((letter) => {
      const interval = setInterval(() => {
        letter.style.transform = `translateX(${Math.floor(Math.random() * 5)}px) 
                                  translateY(${Math.floor(Math.random() * 5)}px) 
                                  rotate(${Math.floor(Math.random() * 5)}deg)`;
      }, 100);

      setTimeout(() => clearInterval(interval), 5000);
    });
  };

  const shakeLetters = "blazing"
    .split("")
    .map((letter, index) => (
      <span
        key={index}
        ref={(el) => (lettersRef.current[index] = el)}
        style={{
          display: "inline-block",
          color: fireColors[Math.floor(Math.random() * fireColors.length)],
          transition: "color 0.5s ease-in-out",
        }}
      >
        {letter}
      </span>
    ));

  return (
    <div className="Landing">
      <title>Home | OSGL</title>
      <div className="main-content">
        <Topbar />
        <h1 className="big-title">
          <span style={{ fontFamily: "'PixelFont', sans-serif" }}>Draw </span>
          <span
            className={`animated-word ${isAnimating ? "slide-out-second" : "slide-in-second"}`}
            style={{ color: colours[colorIndex[0]], fontFamily: "'PixelFont', sans-serif" }}
          >
            {words[indexExtra]}
          </span>
          <span style={{ fontFamily: "'PixelFont', sans-serif" }}>, </span>
          <span
            className={`animated-word ${isAnimating ? "slide-out" : "slide-in"}`}
            style={{ color: colours[colorIndex[1]], fontFamily: "'PixelFont', sans-serif" }}
          >
            {words[index]}
          </span>
          <span style={{ fontFamily: "'PixelFont', sans-serif" }}> and more </span>
          <br />
          <span style={{ fontFamily: "'PixelFont', sans-serif" }}>at </span>
          <span className="shake" style={{ fontFamily: "'PixelFont', sans-serif" }}>{shakeLetters}</span>
          <span style={{ fontFamily: "'PixelFont', sans-serif" }}> speeds.</span>
        </h1>
        <h1 className="big-subtitle">
          A fast, easy-to-use graphics library for Roblox,
          <br></br>
          built for rendering to EditableImages.
        </h1>
        {/* New button */}
        <button
  className="download-button"
  onClick={() => window.open("https://github.com/osgl-rbx/osgl/releases/latest", "_blank")}
>
  DOWNLOAD
</button>

        <div className="dotted-line-container">
          <div className="dotted-line"></div>
          <div className="dotted-arrow"></div>
          <span className="line-text" style={{ fontFamily: "'PixelFont', sans-serif" }}>Get the RBXM!</span>
        </div>

        <h1 className="big-title-2">
          <span style={{ fontFamily: "'PixelFont', sans-serif" }}>Lightning fast</span>
          <h1 className="big-subtitle-2">Enjoy pixel-perfect rendering, realtime.</h1>
          <img src="/osgl/LightningBolt.png" alt="Lightning" className="big-title-2-image"></img>
          <img src="/osgl/sample.png" alt="Sample" className="big-title-2-image-opposite"></img>
        </h1>

        <h1 className="big-title-3">
          <span style={{ fontFamily: "'PixelFont', sans-serif" }}>User-friendly</span>
          <h1 className="big-subtitle-3">Easy to use, right out of the box.</h1>
          <img src="/osgl/Smiley.png" alt="Face" className="big-title-3-image"></img>
          <img src="/osgl/apiExample.png" alt="API" className="big-title-3-image-opposite"></img>
          <img src="/osgl/apiExample.png" alt="FOOTER" className="big-title-3-image-opposite-invis"></img>
        </h1>
      </div>

      <Footer />
      <div className="grid-background"></div>
    </div>
  );
};

export default Landing;