import React, { Suspense, lazy } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import sidebarInfo from "./sidebar-info";
import APITopbar from "./components/api-topbar";
import { Sidebar } from "lucide-react";
import Footer from "../components/footer";

const API = () => {
  return (
    <Suspense fallback={<div>
      <div className="API">
    <title>API | OSGL</title>
    <APITopbar />

    <div className="sidebar-content">
        <Sidebar links={sidebarInfo} />
    </div>

    <div className="api-content">
        
    </div>
    <Footer />
    <div className="grid-background-dark"></div>
</div>
    </div>}>
    <Routes>
        <Route index element={<Navigate to="window" replace />} />
        {sidebarInfo.map((element) => {
          const PageComponent = lazy(() => import(`./pages/${element.file}.js`));
          return (
            <Route
              key={element.href}
              path={element.href.split("osgl/#/api/")[1]}
              element={<PageComponent />}
            />
          );
        })}
      </Routes>
    </Suspense>
  );
};

export default API;