import React, { useState } from "react";

const TopbarButton = ({name, link}) => {
    const [isHovered, setIsHovered] = useState(false);

    let noColour = `/osgl/${name}-no-colour.png`;
    let colour = `/osgl/${name}-colour.png`;

    return <div
        className={name}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={() => window.location.href = link || ""}
    >
        <img
            src={noColour}
            alt={name}
            className={`${name}-img ${isHovered ? `${name}-hover` : ""}`}
        />
        <img
            src={colour}
            alt={name}
            className={`${name}-img ${isHovered ? "" : `${name}-hover`}`}
        />
    </div>
}

export default TopbarButton;