import React, { useEffect, useState } from "react";
import "../css/footer.css";

const Footer = () => {
  const [isFooterVisible, setIsFooterVisible] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY + window.innerHeight;
      const pageHeight = document.documentElement.scrollHeight;

      if (pageHeight <= window.innerHeight) {
        setIsFooterVisible(false);
      } 

      else if (scrollPosition >= pageHeight - 50) {
        setIsFooterVisible(true);
      } else {
        setIsFooterVisible(false);
      }
    };
  
    window.addEventListener("scroll", handleScroll);
    handleScroll(); 
  
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <footer className={isFooterVisible ? "footer visible" : "footer"}>
      <div className="footer-content">
        <p className="footer-text" style={{fontFamily: "'PixelFont', sans-serif"}}>© 2025 OSGL. All rights reserved.</p>
        <div className="footer-links">
          <a href="https://github.com/osgl-rbx/osgl/blob/main/CONTRIBUTING.md" className="footer-link" style={{fontFamily: "'PixelFont', sans-serif"}}>Contributing</a>
          <a href="https://github.com/osgl-rbx/osgl" className="footer-link" style={{fontFamily: "'PixelFont', sans-serif"}}>Source</a>
          <a href="https://discord.com/invite/syCDZBvM" className="footer-link" style={{fontFamily: "'PixelFont', sans-serif"}}>Discord</a>
        </div>
      </div>
    </footer>
  );
};

export default Footer;